@import url(https://fonts.googleapis.com/css?family=Roboto:700);
.slick-prev {
  z-index: 1;
  left: 20px;
}

.slick-next {
  right: 20px;
}
.slick-prev:before,
.slick-next:before {
  color: black;
}

body {
  margin: 0px;
  font-family: "Roboto";
  text-align: center;
}

#container {
  color: #ffff;
  text-transform: uppercase;
  font-size: 36px;
  font-weight: bold;
  position: fixed;
  bottom: 40%;
  padding-left: 5%;
  display: block;
  position: absolute;
}

#flip {
  height: 50px;
  overflow: hidden;
}

#flip > div > div {
  color: #fff;
  padding: 4px 12px;
  height: 45px;
  margin-bottom: 45px;
  display: inline-block;
}

#flip div:first-child {
  -webkit-animation: show 10s linear infinite;
          animation: show 10s linear infinite;
}

#flip div div {
  background: #42c58a;
}
#flip div:first-child div {
  background: #4ec7f3;
}
#flip div:last-child div {
  background: #dc143c;
}

@-webkit-keyframes show {
  0% {
    margin-top: -270px;
  }
  5% {
    margin-top: -180px;
  }
  33% {
    margin-top: -180px;
  }
  38% {
    margin-top: -90px;
  }
  66% {
    margin-top: -90px;
  }
  71% {
    margin-top: 0px;
  }
  99.99% {
    margin-top: 0px;
  }
  100% {
    margin-top: -270px;
  }
}

@keyframes show {
  0% {
    margin-top: -270px;
  }
  5% {
    margin-top: -180px;
  }
  33% {
    margin-top: -180px;
  }
  38% {
    margin-top: -90px;
  }
  66% {
    margin-top: -90px;
  }
  71% {
    margin-top: 0px;
  }
  99.99% {
    margin-top: 0px;
  }
  100% {
    margin-top: -270px;
  }
}

.ant-form-item-label label {
  color: white;
}

.ant-form-item-label {
  width: 20%;
}

.ant-card-head {
  color: white;
}

.ant-form-item {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}

.ant-form-item-control-wrapper {
  width: 60%;
}

.ant-input {
  background-color: transparent;
  border: 0;
  border-bottom: 2px solid white;
  border-radius: 0;
  color: white;
}

.ant-input:focus {
  box-shadow: none;
  border-color: rgb(64, 169, 255);
}

.has-error .ant-input:focus {
  box-shadow: none;
  border-color: #f5222d;
}

.ant-form-item-control {
  margin-left: 3%;
}
.ant-select-selection {
  background-color: transparent;
  border: 0;
  border-bottom: 2px solid white;
  border-radius: 0;
  color: white;
  box-shadow: none;
}
.ant-select-focused .ant-select-selection,
.ant-select-selection:focus,
.ant-select-selection:active {
  border-color: rgb(64, 169, 255);
  box-shadow: none;
}

